import styled from "styled-components"
import theme from "./../styles/theme"
import { device } from "../media-queries"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100vw;
  background-color: ${theme.colors.creamyWhite};
  overflow: hidden;
  padding: 0 20px;

  @media ${device.mobileS} {
    height: 500px;
  }
  @media ${device.tablet} {
    height: 700px;
  }
`

const PageHeading = styled.div`
  font-size: ${theme.fontSizes.head32};
  font-family: DroidSans-Bold;
  color: ${theme.colors.darkBlue};

  @media ${device.mobileS} {
    margin: 20px auto 80px;
  }
  @media ${device.tablet} {
    margin: 135px 0 80px;
  }
`

const EmailInput = styled.input`
  border: 1px solid #cccccc;
  border-radius: 5px;
  color: ${theme.colors.black};
  font-size: ${theme.fontSizes.n14};
  font-family: DroidSans;
  padding: 5px 10px;
  outline: none;

  width: 100%;
  max-width: 500px;
`

const MessageInput = styled.textarea`
  border: 1px solid #cccccc;
  border-radius: 5px;
  color: ${theme.colors.black};
  font-size: ${theme.fontSizes.n14};
  font-family: DroidSans;

  width: 100%;
  max-width: 500px;

  min-height: 125px;
  max-height: 125px;

  padding: 5px 10px;
  margin-top: 20px;
  outline: none;
`

const Button = styled.div`
  margin-top: 20px;
  cursor: pointer;
  border-radius: 15px;
  background-color: ${theme.colors.darkBlue};
  color: ${theme.colors.white};
  font-family: DroidSans;
  font-size: ${theme.fontSizes.n14};
  padding: 15px;

  &:hover {
    background-color: ${theme.colors.bioBg};
  }
`

export { Container, PageHeading, EmailInput, MessageInput, Button }
