import styled from "styled-components"
import theme from "./../styles/theme"
import { device } from "../media-queries"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100vh;
  width: 100vw;
  min-height: 768px;
  background-color: ${theme.colors.darkBlue};
  overflow: hidden;

  @media ${device.mobileS} {
    height: 1400px;
  }
  @media ${device.laptop} {
    height: 1200px;
  }
`

const BioContainer = styled.div`
  position: absolute;

  display: flex;
  flex-direction: column;
  color: ${theme.colors.white};
  background-color: ${theme.colors.bioBg};

  @media ${device.mobileS} {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  @media ${device.tablet} {
    top: 100px;
    right: 0;
    bottom: 0;
    left: 40vw;
  }
`

const Bold = styled.span`
  font-family: Roboto-Bold;
`

const Heading = styled.div`
  font-family: DroidSans-Bold;
  font-size: ${theme.fontSizes.head32};
  text-align: center;
  margin: 40px auto;
`

const Text = styled.p`
  font-family: Roboto-Light;
  font-size: ${theme.fontSizes.lg18};
  padding: 0 40px;
  letter-spacing: 0.2px;
`

const Badges = styled.div`
  @media ${device.mobileS} {
    display: none;
  }
  @media ${device.laptop} {
    display: flex;
    flex-direction: column;
    z-index: 1;
    justify-content: space-between;
    align-items: flex-end;

    position: absolute;
    top: 300px;
    left: 190px;
    bottom: 350px;
  }

  @media ${device.laptopL} {
    right: 59vw;
  }
`

export { Container, BioContainer, Bold, Heading, Text, Badges }
