import { css } from "styled-components"

import DroidSans from "../fonts/droid-sans/DroidSans.ttf"
import DroidSansBold from "../fonts/droid-sans/DroidSans-Bold.ttf"
import Michroma from "../fonts/michroma/Michroma.ttf"
import RobotoRegular from "../fonts/roboto/Roboto-Regular.ttf"
import RobotoBold from "../fonts/roboto/Roboto-Bold.ttf"
import RobotoLight from "../fonts/roboto/Roboto-Light.ttf"

const FontFaces = css`
  @font-face {
    font-family: "DroidSans";
    src: url(${DroidSans}) format("truetype");
    font-weight: 300;
    font-style: normal;
  }
  @font-face {
    font-family: "DroidSans-Bold";
    src: url(${DroidSansBold}) format("truetype");
    font-weight: 700;
    font-style: normal;
  }
  @font-face {
    font-family: "Michroma";
    src: url(${Michroma}) format("truetype");
    font-weight: 300;
    font-style: normal;
  }
  @font-face {
    font-family: "Roboto";
    src: url(${RobotoRegular}) format("truetype");
    font-weight: 300;
    font-style: normal;
  }
  @font-face {
    font-family: "Roboto-Bold";
    src: url(${RobotoBold}) format("truetype");
    font-weight: 700;
    font-style: normal;
  }
  @font-face {
    font-family: "Roboto-Light";
    src: url(${RobotoLight}) format("truetype");
    font-weight: 100;
    font-style: normal;
  }
`

export default FontFaces
