import React from "react"
import * as Elements from "./elements"
import {
  Greeting,
  Name,
  Description,
  Button,
  IntroContent,
  IntroContainerInner,
} from "./intro.styles"
import useScrollTo from "../hooks/scrolling.hook"

function Intro() {
  const scrollToContact = useScrollTo("contact")

  return (
    <IntroContainer>
      <Greeting>Welcome, my name is</Greeting>
      <Name>Rostyslav Solopatych</Name>
      <Description>
        <div>I am a software engineer specializing in Web development.</div>
        <div>Transforming your ideas into working Web and mobile apps.</div>
      </Description>
      <Button onClick={scrollToContact}>REACH ME OUT</Button>
    </IntroContainer>
  )
}

const IntroContainer = ({ children }) => (
  <IntroContainerInner>
    <IntroContent>{children}</IntroContent>
    <Elements.RightCircle />
    <Elements.YellowCircle />
    <Elements.BottomCircle />
    <Elements.ThreeDots />
    <Elements.BigWhiteDot />
    <Elements.LongCurve />
    <Elements.ShortCurve />
    <Elements.SmallRing />
    <Elements.SmallWhiteDot />
    <Elements.ManyDots
      style={{ position: "absolute", bottom: "0", right: "20vw" }}
    />
  </IntroContainerInner>
)

export default Intro
