import React from "react"
import {
  Container,
  PageHeading,
  EmailInput,
  MessageInput,
  Button,
} from "./contact.styles"

function Contact() {
  // TODO: email validation
  // TODO: message sanitization
  return (
    <Container>
      <PageHeading id="contact">Reach me out</PageHeading>
      <EmailInput placeholder="Your email" type="email" />
      <MessageInput placeholder="Your message" />
      <Button>SEND MESSAGE</Button>
    </Container>
  )
}

export default Contact
