import { device } from "../media-queries"

import styled from "styled-components"
import theme from "../styles/theme"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100vw;
  min-height: 800px;
  overflow: hidden;
  background-color: ${theme.colors.paleYellow};
`

const Heading = styled.div`
  color: ${theme.colors.darkBlue};
  font-family: DroidSans-bold;
  font-size: ${theme.fontSizes.head32};
  text-align: center;
  margin: 80px auto;
`

const OfferList = styled.div`
  z-index: 2;

  @media ${device.mobileS} {
    display: flex;
    flex-direction: column;
    height: 1000px;
  }
  @media ${device.tablet} {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    height: initial;
  }
`

const OfferWithText = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;

  &:nth-child(2) {
    margin-left: 5vw;
    margin-right: 5vw;
  }
`

const OfferText = styled.div`
  color: ${theme.colors.darkBlue};
  font-family: DroidSans-Bold;
  font-size: ${theme.fontSizes.xlg20};
`

export { Container, Heading, OfferList, OfferWithText, OfferText }
