import React, { useState } from "react"
import {
  Bold,
  Bullet,
  Container,
  Content,
  Description,
  Heading,
  Highlight,
  PageHeading,
  Project,
  ProjectBullet,
  Subheading,
  SwitcherContainer,
  Technologies,
  Timeline,
} from "./resume.styles"

const bullets = [
  { label: "Capgemini" },
  { label: "Anixe" },
  { label: "Selerant" },
]

function Resume() {
  const [selectedBullet, setSelectedBullet] = useState(bullets[0])
  const isSelected = b => b.label === selectedBullet.label

  return (
    <Container>
      <PageHeading>My Experience</PageHeading>
      <SwitcherContainer>
        <Timeline>
          {bullets.map(b => (
            <Bullet
              key={b.label}
              isSelected={isSelected(b)}
              onClick={() => setSelectedBullet(b)}
            >
              {b.label}
            </Bullet>
          ))}
        </Timeline>
        {isSelected(bullets[0]) && (
          <Content>
            <Heading>
              Senior Software Engineer
              <Highlight>@ Capgemini</Highlight>
            </Heading>
            <Subheading>Sep 2018 - present</Subheading>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>
                  Corporate PDF reporting service for investment bank in US
                </Highlight>
              </ProjectBullet>
              <Description>
                Web application for generation of compliance reports in PDF
                format. Reports contained information on compliance with
                corporate digitalization standards for various bank departments.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                Implemented the high-end pixel-perfect UI design of the report
                content which was shared across 5 corporate domains with{" "}
                <Bold>more than 400 departments/sub-departments</Bold> and used
                in digitalization cost planning involving{" "}
                <Bold>dozens of millions of dollars</Bold>.
                <Technologies>
                  <Bold>Technologies</Bold>: Svelte, NodeJS, JavaScript ES6,
                  SCSS
                </Technologies>
              </Description>
            </Project>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>
                  Corporate hyperlink scanning service for investment bank in US
                </Highlight>
              </ProjectBullet>
              <Description>
                Web application for lookup and testing of hyperlinks on corporal
                website and internal web applications.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                <Bold>Lead a team</Bold> of 3 developers which{" "}
                <Bold>built and shipped the service from scratch</Bold>.
                Collected and analysed requirements by contacting users directly
                on regular basis. Desinged UI/UX of the Web app. Implemented the
                app in full-stack fashion with 70%-30% frontend-backend
                distribution. Service was used by central publishing department
                of the bank involving <Bold>more than 50 content editors</Bold>{" "}
                and allowed to find <Bold>more than 300 invalid links</Bold> on
                bank’s production website.
                <Technologies>
                  <Bold>Technologies</Bold>: ReactJS, NodeJS, TypeScript, SCSS
                </Technologies>
              </Description>
            </Project>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>
                  Corporate digitalization planning service for investment bank
                  in US
                </Highlight>
              </ProjectBullet>
              <Description>
                Web application for monitoring and planning of digitalization
                and modernization of various bank departments.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                Implemented core functionality for creation and storage of
                department plan. Introduced UI/UX changes and bugfixes.
                Implemented the app in full-stack fashion with 50%-50%
                frontend-backend distribution.
                <Technologies>
                  <Bold>Technologies:</Bold> ReactJS, NodeJS, TypeScript, SCSS
                </Technologies>
              </Description>
            </Project>
          </Content>
        )}
        {isSelected(bullets[1]) && (
          <Content>
            <Heading>
              Frontend Developer
              <Highlight>@ Anixe</Highlight>
            </Heading>
            <Subheading>Jun 2017 - Sep 2018</Subheading>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>Hotel Extranet</Highlight>
              </ProjectBullet>
              <Description>
                Web application that allows travel agents to upload their
                partner hotels or suppliers and give them access to the online
                booking system so that the partners can put their hotel’s
                description and information about room availability, rates,
                special deals, and restrictions.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                Implemented core functionality of the UI which allowed to{" "}
                <Bold>launch the application live</Bold>. The application gives
                access to <Bold>thousands of hotels</Bold> for{" "}
                <Bold>more than 100 users</Bold>.
                <Technologies>
                  <Bold>Technologies</Bold>: Angular, TypeScript, ES6, SCSS
                </Technologies>
              </Description>
            </Project>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>Bizpartners</Highlight>
              </ProjectBullet>
              <Description>
                Web application that gives access to hotel suppliers information
                and information on physical hotels.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                Implemented core functionality of the UI and added minor changes
                to the backend.
                <Technologies>
                  <Bold>Technologies</Bold>: Angular, NodeJS, TypeScript, ES6,
                  SCSS
                </Technologies>
              </Description>
            </Project>
          </Content>
        )}
        {isSelected(bullets[2]) && (
          <Content>
            <Heading>
              Junior Web Developer
              <Highlight>@ Selerant</Highlight>
            </Heading>
            <Subheading>Nov 2015 - May 2016</Subheading>
            <Project>
              <ProjectBullet withBullet={true}>
                Project
                <Highlight>DevEx</Highlight>
              </ProjectBullet>
              <Description>
                Formula-based product lifecycle management (PLM) software
                pertains to manufacturers in process industries such as food and
                beverage, flavor and fragrances, personal care and specialty
                chemicals companies.
              </Description>
              <ProjectBullet>My contribution</ProjectBullet>
              <Description>
                Added minor changes to backend and frontend.
                <Technologies>
                  <Bold>Technologies</Bold>: JavaScript, jQuery, C# .NET 4.5,
                  Oracle DB
                </Technologies>
              </Description>
            </Project>
          </Content>
        )}
      </SwitcherContainer>
    </Container>
  )
}

export default Resume
