import React from "react"
import * as Elements from "./elements"
import {
  Container,
  Heading,
  OfferList,
  OfferWithText,
  OfferText,
} from "./offer.styles"

function Offer() {
  return (
    <Container>
      <Elements.ManyDots
        style={{ position: "absolute", top: "0", right: "20vw" }}
      />
      <Heading>What I do</Heading>
      <OfferList>
        <OfferWithText>
          <Elements.DesignOffer />
          <OfferText>UI Design</OfferText>
        </OfferWithText>
        <OfferWithText>
          <Elements.DevOffer />
          <OfferText>Development</OfferText>
        </OfferWithText>
        <OfferWithText>
          <Elements.DevOpsOffer />
          <OfferText>DevOps</OfferText>
        </OfferWithText>
      </OfferList>
      <Elements.OfferBottomElipse />
      <Elements.OfferBottomRect />
    </Container>
  )
}

export default Offer
