import styled from "styled-components"
import { headerHeight } from "../config"
import theme from "../styles/theme"
import { device } from "../media-queries"

const Greeting = styled.div`
  padding: 125px 4vw 10px;
  color: ${theme.colors.orange};
  font-family: DroidSans;
  font-size: ${theme.fontSizes.lg18};

  @media ${device.mobileS} {
    padding-left: 20px;
    padding-right 20px;
  }
  @media ${device.tablet} {
    padding-left: 4vw;
    padding-right: 4vw;
  }
  @media ${device.laptop} {
    padding-left: 4vw;
    padding-right: 4vw;
  }
  @media ${device.laptopL} {
    padding-left: 6vw;
    padding-right: 6vw;
  }
  @media ${device.desktop} {
    padding-left: 10vw;
    padding-right 10vw;
  }
`

const Name = styled.div`
  font-size: ${theme.fontSizes.head32};
  font-family: Roboto-Bold;
  line-height: 1.1;
  color: ${theme.colors.black};

  @media ${device.mobileS} {
    padding-right: 20px;
    padding-left: 20px;
  }
  @media ${device.tablet} {
    padding-right: 4vw;
    padding-left: 4vw;
  }
  @media ${device.laptop} {
    padding-right: 4vw;
    padding-left: 4vw;
  }
  @media ${device.laptopL} {
    padding-right: 6vw;
    padding-left: 6vw;
  }
  @media ${device.desktop} {
    padding-right: 10vw;
    padding-left: 10vw;
  }
`

const Description = styled.div`
  padding: 30px 0 0 4vw;
  font-size: ${theme.fontSizes.lg18};
  font-family: Roboto;
  color: ${theme.colors.black};

  @media ${device.mobileS} {
    padding-right: 20px;
    padding-left: 20px;
  }
  @media ${device.tablet} {
    padding-left: 4vw;
    padding-right: 4vw;
  }
  @media ${device.laptop} {
    padding-right: 4vw;
    padding-left: 4vw;
  }
  @media ${device.laptopL} {
    padding-right: 6vw;
    padding-left: 6vw;
  }
  @media ${device.desktop} {
    padding-right: 10vw;
    padding-left: 10vw;
  }
`

const Button = styled.div`
  margin: 50px 0 0 4vw;
  cursor: pointer;
  border-radius: 15px;
  background-color: ${theme.colors.black};
  color: ${theme.colors.white};
  font-family: DroidSans;
  font-size: ${theme.fontSizes.n14};
  padding: 15px;

  width: 128px;

  &:hover {
    background-color: ${theme.colors.darkBlue};
  }

  @media ${device.mobileS} {
    margin-left: 20px;
    margin-right: 20px;
  }
  @media ${device.tablet} {
    margin-left: 4vw;
    margin-right: 4vw;
  }
  @media ${device.laptop} {
    margin-left: 4vw;
    margin-right: 4vw;
  }
  @media ${device.laptopL} {
    margin-left: 6vw;
    margin-right: 6vw;
  }
  @media ${device.desktop} {
    margin-left: 10vw;
    margin-right: 10vw;
  }
`

const IntroContent = styled.div`
  position: absolute;
  top: ${headerHeight}px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;

  display: flex;
  flex-direction: column;
`

const IntroContainerInner = styled.div`
  position: relative;
  height: 100vh;
  width: 100vw;
  min-height: 768px;
  overflow: hidden;
  background-color: ${theme.colors.paleYellow};
`

export {
  Greeting,
  Name,
  Description,
  Button,
  IntroContent,
  IntroContainerInner,
}
