import styled from "styled-components"
import theme from "./../styles/theme"
import { device } from "../media-queries"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 1120px;
  width: 100vw;
  background-color: ${theme.colors.creamyWhite};
  overflow: hidden;
`

const PageHeading = styled.div`
  margin: 80px auto;
  font-size: ${theme.fontSizes.head32};
  font-family: DroidSans-Bold;
  color: ${theme.colors.darkBlue};
`

const SwitcherContainer = styled.div`
  display: flex;
  height: 100%;
  align-items: stretch;

  @media ${device.tablet} {
    margin: 0 40px;
  }
  @media ${device.laptop} {
    margin: 0 15vw;
  }
`

const Timeline = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: space-between;

  position: relative;

  min-height: 900px;

  &::after {
    content: "";

    position: absolute;
    top: 5px;
    bottom: 5px;
    right: 14px;

    border-right: 2px solid ${theme.colors.orange};
  }
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  flex: 5;
  padding-left: 25px;
`

const Bullet = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;

  color: ${props =>
    props.isSelected ? theme.colors.darkBlue : theme.colors.orange};
  font-family: DroidSans-Bold;
  font-size: ${theme.fontSizes.lg18};
  cursor: pointer;

  &:hover {
    color: ${theme.colors.darkBlue};
  }

  &::after {
    content: "";

    display: block;
    z-index: 1;

    width: 30px;
    height: 30px;
    margin-left: 25px;

    border-radius: 50%;
    border: 3px solid ${theme.colors.creamyWhite};

    transition: background-color 0.5s linear;
    background-color: ${props =>
      props.isSelected ? theme.colors.darkBlue : theme.colors.orange};
  }
`

const Heading = styled.div`
  display: flex;
  align-items: baseline;
  line-height: 30px;

  font-family: Roboto-Bold;
  font-size: ${theme.fontSizes.lg18};
  color: ${theme.colors.darkBlue};
`

const Subheading = styled.div`
  color: ${theme.colors.darkBlue};
  font-family: Roboto;
  font-size: ${theme.fontSizes.n14};
  letter-spacing: 1px;
  margin-left: 10px;
  margin-bottom: 20px;
`

const Highlight = styled.div`
  font-family: inherit;
  font-size: inherit;
  color: ${theme.colors.orange};
  margin-left: 10px;
`
const Project = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 30px;
`

const ProjectHeading = styled.div``

const ProjectBullet = styled.div`
  position: relative;
  display: flex;
  font-family: Roboto-Bold;
  font-size: ${theme.fontSizes.n14};
  color: ${theme.colors.darkBlue};

  ${props =>
    props.withBullet &&
    `
      &::before {
        content: "";

        position: absolute;
        left: -20px;
        top: 7px;

        border-left: 10px solid ${theme.colors.darkBlue};
        border-top: 5px solid transparent;
        border-bottom: 5px solid transparent;
        width: 0;
        height: 0;
      }
  `}
`

const ProjectTitle = styled.div``

const Description = styled.div`
  font-family: Roboto;
  font-size: ${theme.fontSizes.n14};
  color: ${theme.colors.darkBlue};
  margin: 12px 0 12px 55px;
`

const Bold = styled.span`
  font-family: Roboto-Bold;
  font-size: inherit;
  color: inherit;
`

const Technologies = styled.p`
  margin-top: 10px;
`

export {
  PageHeading,
  SwitcherContainer,
  Container,
  Timeline,
  Content,
  Bullet,
  Heading,
  Subheading,
  Highlight,
  Project,
  ProjectHeading,
  ProjectBullet,
  ProjectTitle,
  Description,
  Bold,
  Technologies,
}
