import React from "react"
import {
  Badges,
  BioContainer,
  Bold,
  Container,
  Heading,
  Text,
} from "./bio.styles"
import * as Elements from "./elements"

function Bio() {
  return (
    <Container id="about">
      <BioContainer>
        <Heading>Who am I</Heading>
        <Text>
          I am a Senior Software Engineer and Web development trainer from
          Ukraine.
        </Text>
        <Text>
          Being a <Bold>top 5% Stack Overflow answerer</Bold> in ReactJS and top
          10% in JavaScript I enjoy solving problems and helping out people
          around the globe.
        </Text>
        <Text>
          My main competence is full-stack development of Web applications with
          a strong emphasis on frontend and UI. However, I am also familiar with
          hybrid mobile development with React Native and Flutter.
        </Text>
        <Text>
          I am a <Bold>verified ReactJS Expert</Bold> and attendee of many JS
          conferences and events including Frontend Connect international
          conference and annual NG Poland Angular conference.
        </Text>
        <Text>
          Programming and algorithms have never been strangers to me as I have
          two degrees: Bachelor's degree in System Analysis and Master's degree
          in Computer Engineering. I am passionate about machine learning and
          especially data mining. Familiar with Python, SciPy, NumPy, and
          Pandas.
        </Text>
        <Text>
          No matter what programming language you use, you have to{" "}
          <Bold>ship your product</Bold>. And I am here for you :) I work with
          Docker and AWS to manage the DevOps and make sure that{" "}
          <Bold>your app is reliable, efficient, secure and cost-optimal</Bold>.
        </Text>
      </BioContainer>
      <Elements.BioTriangle />
      <Elements.BioCombo1 style={{ position: "absolute", top: "100px" }} />
      <Elements.BioCombo2 style={{ position: "absolute", top: "500px" }} />
      <Badges>
        <Elements.Top5
          href="https://stackoverflow.com/users/story/7320665"
          target="_blank"
          rel="noopener noreferrer"
        />
        <Elements.Top10
          href="https://stackoverflow.com/users/story/7320665"
          target="_blank"
          rel="noopener noreferrer"
        />
        <Elements.PluralsightIQ
          href="https://stackoverflow.com/users/story/7320665"
          target="_blank"
          rel="noopener noreferrer"
        />
      </Badges>
      <Elements.BioBottomElipse />
    </Container>
  )
}

export default Bio
